___CSS_LOADER_EXPORT___.push([module.id, `.dashboard {
    display: flex;
    justify-content: space-between;
    gap: 20px; /* 패널 간의 간격을 통일 */
  }
  
  .panel {
    background-color: #28293d; /* 대시보드와 일관된 색상 */
    padding: 20px;
    border-radius: 10px;
    color: white;
    flex: 1; /* 각 패널이 동일한 크기를 가지도록 설정 */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .camera-feed {
    flex: 1;
  }
  
  .video-container {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .video-container video {
    width: 100%;
    height: 150px; /* 각 비디오의 높이를 설정 */
    object-fit: cover; /* 비디오가 컨테이너에 맞게 잘리도록 설정 */
    border-radius: 5px;
    background-color: #3b3b5a; /* 비디오가 로드되지 않을 때의 배경색 */
  }
  
  .notification-panel {
    flex: 1;
  }
  
  .notification-panel ul {
    list-style-type: none;
    padding: 0;
  }
  
  .notification-panel li {
    background-color: #3b3b5a; /* 통일된 색상 */
    padding: 10px;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  
  .notification-panel li:hover {
    background-color: #444; /* 호버 시 약간 더 밝은 색상 */
  }
  
  .stats-section {
    flex: 1;
  }
  
  .stats {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .stat {
    background-color: #3b3b5a; /* 패널과 일관된 색상 */
    padding: 20px;
    border-radius: 5px;
    text-align: center;
  }
  
  .stat-title {
    font-size: 16px;
    color: #cccccc; /* 조금 더 밝은 회색 텍스트 */
  }
  
  .stat-value {
    font-size: 24px;
    font-weight: bold;
    color: #ffffff; /* 흰색 텍스트 */
  }  `, "",{"version":3,"sources":["webpack://./src/components/Report.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,8BAA8B;IAC9B,SAAS,EAAE,iBAAiB;EAC9B;;EAEA;IACE,yBAAyB,EAAE,iBAAiB;IAC5C,aAAa;IACb,mBAAmB;IACnB,YAAY;IACZ,OAAO,EAAE,0BAA0B;IACnC,aAAa;IACb,sBAAsB;IACtB,8BAA8B;EAChC;;EAEA;IACE,OAAO;EACT;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,SAAS;EACX;;EAEA;IACE,WAAW;IACX,aAAa,EAAE,kBAAkB;IACjC,iBAAiB,EAAE,0BAA0B;IAC7C,kBAAkB;IAClB,yBAAyB,EAAE,wBAAwB;EACrD;;EAEA;IACE,OAAO;EACT;;EAEA;IACE,qBAAqB;IACrB,UAAU;EACZ;;EAEA;IACE,yBAAyB,EAAE,WAAW;IACtC,aAAa;IACb,kBAAkB;IAClB,mBAAmB;EACrB;;EAEA;IACE,sBAAsB,EAAE,oBAAoB;EAC9C;;EAEA;IACE,OAAO;EACT;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,SAAS;EACX;;EAEA;IACE,yBAAyB,EAAE,eAAe;IAC1C,aAAa;IACb,kBAAkB;IAClB,kBAAkB;EACpB;;EAEA;IACE,eAAe;IACf,cAAc,EAAE,mBAAmB;EACrC;;EAEA;IACE,eAAe;IACf,iBAAiB;IACjB,cAAc,EAAE,WAAW;EAC7B","sourcesContent":[".dashboard {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    gap: 20px; /* 패널 간의 간격을 통일 */\r\n  }\r\n  \r\n  .panel {\r\n    background-color: #28293d; /* 대시보드와 일관된 색상 */\r\n    padding: 20px;\r\n    border-radius: 10px;\r\n    color: white;\r\n    flex: 1; /* 각 패널이 동일한 크기를 가지도록 설정 */\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: space-between;\r\n  }\r\n  \r\n  .camera-feed {\r\n    flex: 1;\r\n  }\r\n  \r\n  .video-container {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 10px;\r\n  }\r\n  \r\n  .video-container video {\r\n    width: 100%;\r\n    height: 150px; /* 각 비디오의 높이를 설정 */\r\n    object-fit: cover; /* 비디오가 컨테이너에 맞게 잘리도록 설정 */\r\n    border-radius: 5px;\r\n    background-color: #3b3b5a; /* 비디오가 로드되지 않을 때의 배경색 */\r\n  }\r\n  \r\n  .notification-panel {\r\n    flex: 1;\r\n  }\r\n  \r\n  .notification-panel ul {\r\n    list-style-type: none;\r\n    padding: 0;\r\n  }\r\n  \r\n  .notification-panel li {\r\n    background-color: #3b3b5a; /* 통일된 색상 */\r\n    padding: 10px;\r\n    border-radius: 5px;\r\n    margin-bottom: 10px;\r\n  }\r\n  \r